import { Component, OnInit } from '@angular/core';
import { LanguageService } from 'dsr-toolkit';
import * as moment from 'moment';
import { Observable, Subscription, timer } from 'rxjs';
import { WorkerLoginResult } from 'src/app/api/models';
import { OperatorAuthenticationService } from 'src/app/common/services/operator-authentication.service';
import { AdministrationService } from 'src/app/api/services';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  everySecond$: Observable<number> = timer(0, 1000);
  subscriptions: Subscription[] = [];
  currentTime: number;
  currentLanguage: string;
  currentOperator: WorkerLoginResult = null;
  secondsToLogout: number = 0;
  Header:string="";
  constructor(
    private languageService: LanguageService,
    private operatorAuthenticationService: OperatorAuthenticationService,
    private administrationService: AdministrationService,
  ) { }

  ngOnInit(): void {
    this.currentLanguage = this.languageService.getCurrent();
    this.administrationService.getApiAdministrationGetCurrentTenantHeader().subscribe(val=> this.Header=val);
    this.operatorAuthenticationService.currentUser.subscribe(worker => {
      this.currentOperator = worker;
    });
    this.subscriptions.push(
      this.everySecond$.subscribe(val => {
        this.currentTime = Date.now();
        this.secondsToLogout = this.operatorAuthenticationService.secondsLeft;
      })
    );
  }

  get secondsLeftParsed(): string {
    const duration = moment.duration(this.secondsToLogout, 'seconds');
    // const resultstring = moment.utc(duration.asMilliseconds()).format('HH:mm:ss');
    const resultstring = moment.utc(duration.asMilliseconds()).format('mm:ss');
    return resultstring;
  }

  logout() {
    this.operatorAuthenticationService.logout();
  }

  // changeLanguage() {
  //   this.languageService.setCurrent(this.currentLanguage);
  //   // location.reload();
  // }

}
