import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateMessageFormatCompiler } from 'ngx-translate-messageformat-compiler';
import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { DsrToolkitModule, HttpErrorInterceptor } from 'dsr-toolkit';
import { ApiModule } from '../api/api.module';
import { environment } from 'src/environments/environment';
import { LoaderInterceptor } from './interceptors/loader-interceptor.service';
import { LoaderService } from './services/loader.service';
import { SfcNavigatorService } from './services/sfc-navigator.service';
import { DisplayConfigurationService } from './services/display-configuration.service';
import { DsrAppLogoButtonComponent } from './components/dsr-app-logo-button/dsr-app-logo-button.component';
import { OperatorAuthenticationService } from './services/operator-authentication.service';

export function HttpLoaderFactory(http: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader(http);
}

@NgModule({
  declarations: [
    DsrAppLogoButtonComponent
  ],
  imports: [
    CommonModule,
    DsrToolkitModule,
    HttpClientModule,
    ApiModule.forRoot({ rootUrl: environment.apiRootUrl }),
  ],
  exports: [
    DsrToolkitModule,
    ApiModule,
    DsrAppLogoButtonComponent
  ],
  providers: [
    DisplayConfigurationService,
    LoaderService,
    SfcNavigatorService,
    OperatorAuthenticationService,
    { provide: HTTP_INTERCEPTORS, useClass: HttpErrorInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: LoaderInterceptor, multi: true }
  ]
})
export class PTCommonModule { }
