import { Component, Input, OnInit } from '@angular/core';
import { MachineStatusModel } from '../models/machine-status.model';
import * as moment from 'moment';

@Component({
  selector: 'app-form-machine-status-card-list',
  templateUrl: './form-machine-status-card-list.component.html',
  styleUrls: ['./form-machine-status-card-list.component.scss']
})
export class FormMachineStatusCardListComponent implements OnInit {

  @Input() statuses: MachineStatusModel[] = [];
  constructor() { }

  ngOnInit(): void {
    this.statuses = this.statuses?.sort((a, b) => moment(a.startTime).valueOf() - moment(b.startTime).valueOf());
  }

}
