import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NotificationService, SelectItem, SelectItemInterface, ServerSelectSearchFieldComponent, GeneralPromptDialogComponent } from 'dsr-toolkit';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AssignWorkOnRoutingsViewModel, WorkOnRoutingsDTOs, WorkOrderRoutingItemViewModel } from 'src/app/api/models';
import { MachineService, WorkOrderService } from 'src/app/api/services';
import { DisplayConfigurationService } from 'src/app/common/services/display-configuration.service';
import { LoaderService } from 'src/app/common/services/loader.service';
import { OperatorAuthenticationService } from 'src/app/common/services/operator-authentication.service';
import { SfcNavigatorService } from 'src/app/common/services/sfc-navigator.service';
import { ComponentStatusModel } from '../models/component-status.model';
import { WorkOrderModel } from '../models/work-order.model';
import { MatDialog } from '@angular/material/dialog';
import { SelectWorkOrderPanelDialogComponent } from './select-work-order-panel-dialog/select-work-order-panel-dialog.component';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-machine-status-work-order-form',
  templateUrl: './machine-status-work-order-form.component.html',
  styleUrls: ['./machine-status-work-order-form.component.scss']
})
export class MachineStatusWorkOrderFormComponent implements OnInit {
  loading = true;
  status: ComponentStatusModel = null;
  inputValue = '';
  itemValue = null;
  workOrders: WorkOrderModel[] = [];
  workOrderAssigningMethod: number = 0;
  assignedWorkOrderLimit: number = 0;
  unassigningWorkOrderEnabled = false;
  isCurrentActivity = false;
  @ViewChild(ServerSelectSearchFieldComponent) selectCmpnt: ServerSelectSearchFieldComponent = null;

  constructor(
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private machineService: MachineService,
    private loaderService: LoaderService,
    private sfcNavigator: SfcNavigatorService,
    private displayConfigurationService: DisplayConfigurationService,
    private notificationService: NotificationService,
    private workOrderService: WorkOrderService,
    private operatorAuthenticationService: OperatorAuthenticationService,
    private dialog: MatDialog) {
    this.status = this.router.getCurrentNavigation().extras.state as ComponentStatusModel;
    if (this.status == null || !this.operatorAuthenticationService.isLoggedIn) {
      this.goToMachineStatus();
    }
    this.loaderService.getLoading().subscribe((v) => {
      setTimeout(() => this.loading = v);
    });
  }

  goToSfc(): void {
    this.sfcNavigator.goToSfc();
  }

  async ngOnInit(): Promise<void> {
    if (this.status.selected?.length > 0) {
      const workOrders = this.status.selected[0].orders;
      if (workOrders?.length > 0) {
        this.workOrders = workOrders;
      }
    }
    this.workOrderAssigningMethod = await this.displayConfigurationService.getMachineWorkOrderAssigningMethod(this.status.machine.id);
    this.assignedWorkOrderLimit = await this.displayConfigurationService.getAssignedWorkOrderLimit(this.status.machine.id);
    this.unassigningWorkOrderEnabled = await this.displayConfigurationService.getUnassigningWorkOrderEnabled(this.status.machine.id);
    this.isCurrentActivity = this.status.selected.some(a => a.endTime == null);
    if(this.workOrderAssigningMethod == 2 && !(this.workOrders.length>0)){
      this.openSelectWorkOrderPanel();
    }
  }

  goToMachineStatus(): void {
    this.operatorAuthenticationService.resetTimeout();
    this.router.navigate(['..'], { state: this.status, relativeTo: this.route });
  }

  searchFn = this.search.bind(this);

  search(search: string): Observable<WorkOrderRoutingItemViewModel[]> {
    return this.workOrderService
      .postApiWorkOrderSearchWorkOrderRoutings({
        search: search,
        machineId: this.status.machine.id
      })
      .pipe(map((items) => {
        return items.map(i => i as SelectItem)
      }));
  }

  submitItem(wor: WorkOrderRoutingItemViewModel): void {
    // console.log('submitItem value', wor);
    console.log('submitItem before', this.itemValue);
    if (wor != null) {


      // this.itemValue = null;
      // setTimeout(() => {
      //   this.selectCmpnt.clearValue();
      console.log('submitItem after', this.itemValue);
      // });

      const wo: WorkOrderModel = { 
        id: wor.value, 
        orderId: wor.woNumber, 
        opNo: wor.opNo, 
        partNo: wor.partNumber, 
        removable: true, 
        partDescription: wor.partDescription,
        partDetails: wor.partDetails };
      const existingWoIdx = this.workOrders.findIndex(ewo => ewo.id === wo.id);
      if (existingWoIdx > -1) {
        this.workOrders.splice(existingWoIdx, 1);
      } else {
        var existingWo = this.workOrders.findIndex(ewo => ewo.opNo == wo.opNo && ewo.orderId == wo.orderId);
        if(existingWo == -1)  this.workOrders.push(wo);
      }
      setTimeout(() => {
        this.selectCmpnt.selectRef.resetValues();
      });
      setTimeout(() => {
        this.selectCmpnt.openSelect();
      }, 200);
      // this.selectCmpnt.openSelect();
      // console.log(this.selectCmpnt.selectRef.getMatSelectRef());
      // this.selectCmpnt.selectRef.getMatSelectRef().close();
      // this.selectCmpnt.selectRef.getMatSelectRef().focus();
      // this.selectCmpnt.selectRef.getMatSelectRef().open();
      // this.selectRef.getMatSelectRef()open().;)
    }


  }

  removeWO(woId: number, removable: boolean): void {
    if(removable){
      const existingWoIdx = this.workOrders.findIndex(ewo => ewo.id === woId);
      if (existingWoIdx > -1) {
        this.workOrders.splice(existingWoIdx, 1);
      }
      this.selectCmpnt.selectRef.resetValues();
      this.selectCmpnt.openSelect();
    }
    else{
      this.dialog.open(GeneralPromptDialogComponent, {
        data: {
          title: 'machine.removeWorkOrderMessage'
        }
      }).afterClosed().subscribe((result: boolean) => {
        if (result) {
          this.machineService.postApiMachineCreateNewMachineActivity(this.status.machine.id).subscribe(
            (response) => {
              this.notificationService.showSuccessTranslated('machine.addNewActivitySuccessful', 'Ok', null, false, 4000);
              this.status.selected = [];
              this.goToMachineStatus();
            },
            (error) => {

            },
            () => {
            }
          );

        }
      });
    }

  }

  submitText(value: string): void {
    this.inputValue = '';
    this.machineService.postApiMachineValidateWorkOnRouting(value).subscribe(
      (response) => {
        const wo: WorkOrderModel = { orderId: response.number, opNo: response.operation };
        const existingWoIdx = this.workOrders.findIndex(ewo => ewo.orderId === wo.orderId && ewo.opNo === wo.opNo);
        if (existingWoIdx > -1) {
          this.workOrders.splice(existingWoIdx, 1);
        } else {
          this.workOrders.push(wo);
        }
      },
      (error) => {

      },
      () => {
      }
    );
  }

  save(): void {
    if(this.workOrders.length > this.assignedWorkOrderLimit){
      this.notificationService.showWarningTranslated('machine.workOrderLimit', 'Ok', null, false, 4000);
      return;
    }
    const data: AssignWorkOnRoutingsViewModel = {
      machineActivityIds: this.status.selected.map(m => m.id),
      workOnRoutings: this.workOrders.map(wo => ({ number: wo.orderId, operation: wo.opNo, routingId: wo.id } as WorkOnRoutingsDTOs))
    };
    this.machineService.postApiMachineAssignWorkOrder(data).subscribe(
      (response) => {
        if(response != null && response.machineCode != null){
          var message = this.translate.instant('machine.workOrderRoutingAssignNotAllowedExceptionMessage',
            {workOrderNumber: response.wrokOrderNumber, machineCode: response.machineCode});
          this.notificationService.showError(message, 'Ok', false, 8000);
        }
        else{
          this.status.selected = [];
          this.notificationService.showSuccessTranslated('common.savedSuccessfully', 'Ok', null, false, 4000);
          this.goToMachineStatus()
        }
      },
      () => {

       },
      () => {}
    );
  }

  openSelectWorkOrderPanel(): void {
    this.dialog.open(SelectWorkOrderPanelDialogComponent, {
      width: '80vw',
      data: {
        machineId: this.status.machine.id
      }
    }).afterClosed().subscribe((result: WorkOrderRoutingItemViewModel) => {
      if (result) {
        this.submitItem(result)
      }
    });
  }
}
