import { AfterViewInit, Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute } from '@angular/router';
import { NotificationService } from 'dsr-toolkit';
import { interval, Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { MachineDTOs, WorkerLoginResult, WorkLogTransferStatusResult } from 'src/app/api/models';
import { MachineService, WorkOrderService } from 'src/app/api/services';
import { LoaderService } from 'src/app/common/services/loader.service';
import { OperatorAuthenticationService } from 'src/app/common/services/operator-authentication.service';
import { SfcNavigatorService } from 'src/app/common/services/sfc-navigator.service';
import { alphaRipple } from 'src/app/common/utils/color-helper';
import { environment } from 'src/environments/environment';
import { MachineSelectAdminPanelDialogComponent } from '../machine-select-admin-panel-dialog/machine-select-admin-panel-dialog.component';
import { MachineModel } from '../models/machine.model';
import { MachineSelectGridConfiguration } from './machine-select-grid-configurations';
import { FocusRefreshService } from 'src/app/common/services/focus-refresh.service';

@Component({
  selector: 'app-machine-select',
  templateUrl: './machine-select.component.html',
  styleUrls: ['./machine-select.component.scss']
})
export class MachineSelectComponent implements OnInit, OnDestroy, AfterViewInit {
  public static CONFIGURATION_KEY: string = 'DSR_PT_MACHINES_SELECT_PANEL_CONFIGURATION';
  public static CURRENT_GRID_KEY: string = 'DSR_PT_MACHINES_SELECT_PANEL_CURRENT_GRID';
  public static LOGOUT_TIMEOUT_KEY: string = 'DSR_PT_LOGOUT_TIMEOUT';
  machines: MachineModel[] = [];
  machinesConfiguration: { [config: string]: number[] } = {};
  machinesIntegrationStauts: WorkLogTransferStatusResult[] = [];
  currentGrid: [number, number] = null;
  loading = true;
  isLandscape = true;
  hidePassword = true;
  loadingMachines = false;
  loadingWorkLogStatus = false;
  operatorLoginCode: string = "";
  currentOperator: WorkerLoginResult = null;
  workOrderTableDisplayedColumns: string[] = ['number', 'qtyOrdered', 'qtyCompleted', 'qtyToFix', 'qtyCompletedWithLogs', 'qtyRejected'];
  partTableDisplayedColumns: string[] = ['partNumber', 'partDescription', 'partCustomCharacter01'];
  private onDestroy$ = new Subject<void>();
  private subscription: Subscription;
  private sub:Subscription;
  private workLogStatusSubscription: Subscription;
  private blinker:any;
  style = ""
  loginInputFocus = true;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private machineService: MachineService,
    private workOrderService: WorkOrderService,
    private loaderService: LoaderService,
    private sfcNavigator: SfcNavigatorService,
    private notificationService: NotificationService,
    private operatorAuthenticationService: OperatorAuthenticationService,
    private dialog: MatDialog,
    private focuRefreshService: FocusRefreshService) {
    this.loaderService.getLoading().subscribe((v) => {
      setTimeout(() => this.loading = v);
    });
  }

  isSmallGrid(): boolean {
    return this.currentGrid[0] == 1;
  }

  isMediumGrid(): boolean {
    return this.currentGrid[0] >= 3 || this.currentGrid[1] >= 4;
  }

  isBigGrid(): boolean {
    return this.currentGrid[0] >= 4;
  }

  checkForWO()
  {
    console.log(this.machines)
    if(this.machines.length==1)
    {
      if(this.machines[0].workOrderIconAnimation==2)
      {
        if(this.blinker==null)
        {
          this.blinker = interval(1000)
        this.sub = this.blinker.pipe(takeUntil(this.onDestroy$)).subscribe(x=>{
          if(this.style=="")
          {
            this.style="background-color: red;"
          }
          else
          {
            this.style="";
          }
        })
        }
      }
      else
      {
        if(this.sub != null)
          this.sub.unsubscribe()
        this.blinker=null;
        this.style=""
      }
    }
  }

  ngOnInit(): void {
    this.loadingMachines = false;
    this.loadMachinesConfiguration();
    this.loadMachines();
    this.getWorkLogTransferStatus();
    const secondsCounter = interval(60000); // 10s
    // const secondsCounter = interval(30000); // 30s
    // const secondsCounter = interval(1000000000); // inf for debug
    this.subscription = secondsCounter.pipe(takeUntil(this.onDestroy$)).subscribe(n =>{
      if(!this.loadingMachines){
        this.loadingMachines = true;
        this.loadMachines();
      }
    }
    );
    this.focuRefreshService.focus.subscribe(x=>{
      this.loginInputFocus=x;
    })
    const workLogTransferStatusCounter = interval(60000);
    this.workLogStatusSubscription = workLogTransferStatusCounter.pipe(takeUntil(this.onDestroy$)).subscribe(n =>{
      if(!this.loadingWorkLogStatus){
        this.loadingWorkLogStatus = true;
        this.getWorkLogTransferStatus();
      }
    }
    );



    this.operatorAuthenticationService.currentUser.subscribe(user => {
        this.currentOperator = user;
    });
    this.operatorAuthenticationService.currentWorker.pipe(takeUntil(this.onDestroy$)).subscribe(worker => {
      if(this.machines.length == 1){
        this.machineService.postApiMachineAssignWorkerToCurrentActivity({workerId: worker.id, machineId: this.machines[0].id})
        .subscribe(
          (result)=>{
            if (result == true)
              this.notificationService.showSuccessTranslated('machine.workerAssignedSuccessfully', 'Ok', null, false, 4000);
            else
              this.notificationService.showInfoTranslated('machine.workerUnassignedSuccessfully', 'Ok', null, false, 4000);

            this.loadMachines();
          },
          ()=>{},
          ()=>{});
      }

    });
  }
  getWorkLogTransferStatus(): void {
    var machineIds = this.machinesConfiguration[this.currentGridKey].filter(x => x > -1);
    if (machineIds.length > 0) {
      this.workOrderService.postApiWorkOrderGetWorkLogTransferStatus(machineIds).subscribe(
        (result: WorkLogTransferStatusResult[]) => {
          this.machinesIntegrationStauts = result;
          this.machines.forEach((m) => {
            m.itegrationStatus = this.machinesIntegrationStauts?.find(x => x.machineId == m.id)?.transferStatus

          });
        },
        (error) => {
          // TODO: catch error
        },
        () => { this.loadingWorkLogStatus = false;
        }
      );
    }
  }

  ngOnDestroy(): void {
    this.onDestroy$.next();
    this.subscription.unsubscribe();
  }

  ngAfterViewInit(): void {
    this.isLandscape = window.innerWidth >= window.innerHeight;
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.isLandscape = event.target.innerWidth >= event.target.innerHeight;
  }

  get currentGridKey(): string {
    return this.currentGrid[0] + '_' + this.currentGrid[1];
  }

  get machinesList(): MachineModel[] {
    const result: MachineModel[] = [];
    this.machinesConfiguration[this.currentGridKey].forEach((machineId) => {
      var existingMachine = this.machines.find(m => m.id == machineId);
      result.push(existingMachine ?? null);
    });
    // console.log(result);
    return result;
  }

  getGridStyles() {
    return {
      'grid-template-rows': `repeat(${this.isLandscape ? this.currentGrid[0] : this.currentGrid[1]}, 1fr)`,
      'grid-template-columns': `repeat(${this.isLandscape ? this.currentGrid[1] : this.currentGrid[0]}, 1fr)`
    };
  }

  loadMachinesConfiguration(): void {
    const value = localStorage.getItem(MachineSelectComponent.CONFIGURATION_KEY);
    if (value) {
      this.machinesConfiguration = JSON.parse(value) as { [config: string]: number[] };
    }

    let updateGrid = false;

    MachineSelectGridConfiguration.forEach((tuple) => {
      var key = tuple[0] + '_' + tuple[1];
      if (!(key in this.machinesConfiguration)) {
        this.machinesConfiguration[key] = Array<number>(tuple[0] * tuple[1]).fill(-1);
        updateGrid = true;
      }
    });

    if (updateGrid) {
      localStorage.setItem(MachineSelectComponent.CONFIGURATION_KEY, JSON.stringify(this.machinesConfiguration));
    }

    const currentGrid = localStorage.getItem(MachineSelectComponent.CURRENT_GRID_KEY);
    if (currentGrid) {
      this.currentGrid = JSON.parse(currentGrid) as [number, number];
    }

    if (!currentGrid || !(this.currentGridKey in this.machinesConfiguration)) {
      this.currentGrid = MachineSelectGridConfiguration[0];
      localStorage.setItem(MachineSelectComponent.CURRENT_GRID_KEY, JSON.stringify(this.currentGrid));
    }
    console.log(this.currentGrid, this.currentGridKey);
    console.log(this.machinesConfiguration);
  }

  loadMachines(): void {
    var machineIds = this.machinesConfiguration[this.currentGridKey].filter(x => x > -1);
    if (machineIds.length > 0) {
      this.machineService.postApiMachine(machineIds).subscribe(
        (result: MachineDTOs[]) => {
          this.machines = result.map(m => {
            return {
              id: m.id,
              code: m.code,
              name: m.name,
              status: m.status,
              statusDetail: m.statusDetail,
              statusHexColor: m.hexColor || '#ffffff',
              products: !this.isBigGrid() ? m.workOrders?.map(p => p.partNumber + " ("+p.partUm+") "+ p.partDescription +" "+ p.partCustomCharacter01) : m.products,
              workers: m.workers,
              statusIconAnimation: m.statusIconAnimation,
              workOrderIconAnimation: m.workOrderIconAnimation,
              workerIconAnimation: m.workerIconAnimation,
              workOrder: m.workOrders != null ? m.workOrders[0]?.number : [],
              workOrders: m.workOrders,
              itegrationStatus: this.machinesIntegrationStauts?.find(x => x.machineId == m.id)?.transferStatus,
              attachmentUrl: m.attachmentUrl,
              active: m.active
            };
          });
        },
        (error) => {
          // TODO: catch error
        },
        () => { this.loadingMachines = false;
          this.checkForWO();
        }
      );
    }
  }

  alphaRipple(hexColor: string): string {
    return hexColor ? alphaRipple(hexColor) : 'rgba(0,0,0,0)';
  }

  goToSfc(): void {
    this.sfcNavigator.goToSfc();
  }

  goToAttachment(machine:any)
  {
    const url = environment.apiRootUrl + "/api/Machine/GetOpenAttachment?machineId=" + machine.id // wygląda na to że musi tak być trzeba będzie sprawdzać z API czy dobrze się wszystko wysyła
    window.open(url,"_blank")
  }

  goToMachineStatus(machine: MachineModel): void {
    if (machine) {
      if (!machine.active) {
        this.notificationService.showWarningTranslated('machine.inactive', 'Ok', null, false, 4000);
      } else if (this.hasPermission(machine.id)) {
        this.router.navigate([machine.id, 'status'], { state: { machine }, relativeTo: this.route });
        this.operatorAuthenticationService.resetTimeout();
      } else if (this.currentOperator != null) {
        this.notificationService.showWarningTranslated('machine.accessDenied', 'Ok', null, false, 4000);
      } else {
        this.notificationService.showWarningTranslated('machine.loginFirst', 'Ok', null, false, 4000);
      }
    } else {
      this.notificationService.showInfoTranslated('machine.assignMachine', 'Ok', null, false, 4000);
    }
  }

  hasPermission(machineId: number) {
    return this.currentOperator && this.currentOperator?.workerMachinePermissions.map(p => p.machineId).includes(machineId);
  }

  openAdminPanel(): void {
    this.dialog.open(MachineSelectAdminPanelDialogComponent, {
      height: '90vh',
      width: '90vw',
      data: {
        // title: 'common.deleteMessage',
        // message: 'common.deleteMessagePrompt',
        // translateData: { count: records.length }
      }
    }).afterClosed().subscribe((result: boolean) => {
      if (result) {
        this.notificationService.showSuccessTranslated('common.savedSuccessfully', 'Ok', null, false, 4000);
        this.subscription.unsubscribe();
        this.loadMachinesConfiguration();
        this.loadMachines();
        const secondsCounter = interval(60000); // 10s
        // const secondsCounter = interval(30000); // 30s
        this.subscription = secondsCounter.pipe(takeUntil(this.onDestroy$)).subscribe(n =>
          this.loadMachines()
        );
      }
    });
  }

  onLogin(): void {
    this.operatorAuthenticationService.login(this.operatorLoginCode);
    this.operatorLoginCode = "";
  }

  togglePasswordVisibility(): void {
    this.hidePassword = !this.hidePassword;
  }
}
