import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LoaderService {
  private isLoading: BehaviorSubject<boolean> = new BehaviorSubject(false);
  private isHttpLoading: boolean;
  private isGlobalLoading: boolean;
  constructor() { }

  private emit(): void {
    this.isLoading.next(this.isHttpLoading || this.isGlobalLoading);
  }

  setGlobalLoading(isLoading: boolean): void {
    this.isGlobalLoading = isLoading;
    this.emit();
  }

  setHttpLoading(isLoading: boolean): void {
    this.isHttpLoading = isLoading;
    this.emit();
  }

  getLoading(): Observable<boolean> {
    return this.isLoading.asObservable();
  }
}
