import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NotificationService } from 'dsr-toolkit';
import { AssignWorkersModel } from 'src/app/api/models/assign-workers-model';
import { MachineService } from 'src/app/api/services';
import { LoaderService } from 'src/app/common/services/loader.service';
import { OperatorAuthenticationService } from 'src/app/common/services/operator-authentication.service';
import { SfcNavigatorService } from 'src/app/common/services/sfc-navigator.service';
import { ComponentStatusModel } from '../models/component-status.model';
import { WorkerModel } from '../models/worker.model';

@Component({
  selector: 'app-machine-status-worker-form',
  templateUrl: './machine-status-worker-form.component.html',
  styleUrls: ['./machine-status-worker-form.component.scss']
})
export class MachineStatusWorkerFormComponent implements OnInit {
  loading = true;
  status: ComponentStatusModel = null;
  inputValue = '';
  workers: WorkerModel[] = [];

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private machineService: MachineService,
    private loaderService: LoaderService,
    private notificationService: NotificationService,
    private sfcNavigator: SfcNavigatorService,
    private operatorAuthenticationService: OperatorAuthenticationService) {
    this.status = this.router.getCurrentNavigation().extras.state as ComponentStatusModel;
    if (this.status == null || !this.operatorAuthenticationService.isLoggedIn) {
      this.goToMachineStatus();
    }
    this.loaderService.getLoading().subscribe((v) => {
      setTimeout(() => this.loading = v);
    });
  }

  goToSfc(): void {
    this.sfcNavigator.goToSfc();
  }

  ngOnInit(): void {
    if (this.status.selected?.length > 0) {
      const workers = this.status.selected[0].workers;
      if (workers?.length > 0) {
        this.workers = workers;
      }
    }
  }

  goToMachineStatus(): void {
    this.operatorAuthenticationService.resetTimeout();
    this.router.navigate(['..'], { state: this.status, relativeTo: this.route });
  }

  submit(value: string): void {
    this.inputValue = '';
    var user = this.operatorAuthenticationService.currentUserValue;

    this.machineService.postApiMachineValidateWorker(value).subscribe(
      (response) => {
        if (user.role == "operatorPlus" && user.id != response.id){
          this.notificationService.showWarningTranslated('machine.assigningOnlyYourself', 'Ok', null, false, 4000);
          return;
        }

        const worker: WorkerModel = { id: response.id, name: response.firstName, surname: response.lastName };
        const existingWorkerIdx = this.workers.findIndex(ewo => ewo.id === worker.id);
        if (existingWorkerIdx > -1) {
          this.workers.splice(existingWorkerIdx, 1);
        } else {
          this.workers.push(worker);
        }
      },
      (error) => {
        if(error.status == 403)
          this.notificationService.showErrorTranslated('machine.inactiveWorker', 'Ok', null, false, 4000);

      },
      () => {
      }
    );
  }
  RemoveWorker(value: WorkerModel):void
  {
    const WorkerIdx = this.workers.findIndex(ewo => ewo.id === value.id);
    this.workers.splice(WorkerIdx,1)
  }
  save(): void {
    const data: AssignWorkersModel = {
      machineActivityIds: this.status.selected.map(m => m.id),
      workerIds: this.workers.map(w => w.id)
    };
    this.machineService.postApiMachineAssignWorker(data).subscribe(
      () => {
        this.status.selected = [];
        this.notificationService.showSuccessTranslated('common.savedSuccessfully', 'Ok', null, false, 4000);
      },
      () => { },
      () => this.goToMachineStatus()
    );
  }
}
