import { ThisReceiver } from '@angular/compiler';
import { Component, Inject, OnInit } from '@angular/core';
import { FormArray, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DsrValidators, GeneralPromptDialogComponent, SelectItem, SelectItemInterface } from 'dsr-toolkit';
import { pairwise, startWith } from 'rxjs/operators';
import { MachineService } from 'src/app/api/services';
import { OperatorAuthenticationService } from 'src/app/common/services/operator-authentication.service';
import { MachineSelectGridConfiguration } from '../machine-select/machine-select-grid-configurations';
import { MachineSelectComponent } from '../machine-select/machine-select.component';

@Component({
  selector: 'PT-machine-select-admin-panel-dialog',
  templateUrl: './machine-select-admin-panel-dialog.component.html',
  styleUrls: ['./machine-select-admin-panel-dialog.component.scss']
})
export class MachineSelectAdminPanelDialogComponent implements OnInit {

  initialized = false;
  logoutTimeoutFormControl: FormControl;
  gridSelectFormControl: FormControl;
  gridOptions: SelectItemInterface[] = [];
  gridSlots: FormControl[];
  availableMachines: SelectItemInterface[] = [];

  get machineOptions(): SelectItemInterface[] {
    return [new SelectItem(-1, "-")].concat(...this.availableMachines);
  }

  machinesConfiguration: { [config: string]: number[] } = {};

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
    protected matDialogRef: MatDialogRef<GeneralPromptDialogComponent>,
    private operatorAuthenticationService: OperatorAuthenticationService,
    private machineService: MachineService) { }

  ngOnInit(): void {
    this.machineService.getApiMachine().subscribe(result => {
      this.availableMachines = result as SelectItemInterface[];
      const logoutTimeoutValue = localStorage.getItem(MachineSelectComponent.LOGOUT_TIMEOUT_KEY);
      const logoutTimeout = JSON.parse(logoutTimeoutValue ?? '0') as number;
      this.logoutTimeoutFormControl = new FormControl(logoutTimeout, [Validators.required, Validators.min(0), DsrValidators.integer]);
      this.generateGridOptions();
      this.initialized = true;
    })
  }

  generateGridOptions(): void {
    const machinesConfigurationValue = localStorage.getItem(MachineSelectComponent.CONFIGURATION_KEY);
    this.machinesConfiguration = JSON.parse(machinesConfigurationValue) as { [config: string]: number[] };

    const currentGridValue = localStorage.getItem(MachineSelectComponent.CURRENT_GRID_KEY);
    const currentGrid = JSON.parse(currentGridValue) as [number, number];

    MachineSelectGridConfiguration.forEach((tuple) => {
      var value = tuple[0] + '_' + tuple[1];
      var text = tuple[0] + ' x ' + tuple[1];
      this.gridOptions.push(new SelectItem(value, text));
    });

    this.gridSelectFormControl = new FormControl(currentGrid[0] + '_' + currentGrid[1]);
    this.changeGridSlots(null);
    this.gridSelectFormControl.valueChanges.pipe(startWith(this.gridSelectFormControl.value), pairwise()).subscribe(([prev, next]: [any, any]) => {
      this.changeGridSlots(prev);
    })
  }

  changeGridSlots(previousValue: any) {
    // Save previous group temporarily
    if (previousValue) {
      this.gridSlots.forEach((value, index) => {
        this.machinesConfiguration[previousValue][index] = value.value;
      });
    }

    // Generate new grid
    const controls: FormControl[] = [];
    this.machinesConfiguration[this.gridSelectFormControl.value].forEach(value => {
      controls.push(new FormControl(value));
    })
    this.gridSlots = controls;
  }

  getGridStyles() {
    var currentGrid = this.gridSelectFormControl.value.split('_');
    return {
      'grid-template-rows': `repeat(${currentGrid[0]}, 1fr)`,
      'grid-template-columns': `repeat(${currentGrid[1]}, 1fr)`
    };
  }

  clickSave(): void {
    // save grid selection
    const currentGrid = this.gridSelectFormControl.value.split('_') as [number, number];
    localStorage.setItem(MachineSelectComponent.CURRENT_GRID_KEY, JSON.stringify(currentGrid));

    // save grids
    this.gridSlots.forEach((value, index) => {
      this.machinesConfiguration[this.gridSelectFormControl.value][index] = value.value;
    });
    localStorage.setItem(MachineSelectComponent.CONFIGURATION_KEY, JSON.stringify(this.machinesConfiguration));

    // save timeoutValue
    localStorage.setItem(MachineSelectComponent.LOGOUT_TIMEOUT_KEY, JSON.stringify(this.logoutTimeoutFormControl.value));
    this.operatorAuthenticationService.changeTimeout(this.logoutTimeoutFormControl.value);

    this.matDialogRef.close(true);
  }

  clickCancel(): void {
    this.matDialogRef.close(false);
  }
}
