import { AfterViewInit, Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SelectItem, SelectItemInterface } from 'dsr-toolkit';
import * as moment from 'moment';
import { MachineModel } from '../models/machine.model';
import { MachineStatusModel } from '../models/machine-status.model';
import { alphaRipple } from 'src/app/common/utils/color-helper';
import { MatRipple } from '@angular/material/core';
import { ShiftModel } from '../models/shift.model';
import { WorkOrderModel } from '../models/work-order.model';
import { ComponentStatusModel } from '../models/component-status.model';
import { MachineService } from 'src/app/api/services';
import { MachineActivityDTOs } from 'src/app/api/models';
import { LoaderService } from 'src/app/common/services/loader.service';
import { WorkerModel } from '../models/worker.model';
import { SfcNavigatorService } from 'src/app/common/services/sfc-navigator.service';
import { DisplayConfigurationService } from 'src/app/common/services/display-configuration.service';
import { OperatorAuthenticationService } from 'src/app/common/services/operator-authentication.service';
import { interval, Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-machine-status-list',
  templateUrl: './machine-status-list.component.html',
  styleUrls: ['./machine-status-list.component.scss']
})
export class MachineStatusListComponent implements OnInit, AfterViewInit {
  @ViewChildren(MatRipple) rippleList: QueryList<MatRipple>;

  loading = true;
  machineId: number;
  machine: MachineModel = null;

  subscription: Subscription;
  private onDestroy$ = new Subject<void>();

  dates: SelectItemInterface[] = [];
  dateValue: string;

  shifts: SelectItemInterface[] = [];
  shiftValue: number;

  dataSource: MachineStatusModel[] = [];

  selected: number[] = [];

  status: ComponentStatusModel = null;

  machineProductionDisplayMethod: number = -1;

  productionData: any = null;

  initialized: boolean = false;
  SHIFTS: ShiftModel[] = [
    { id: 1, startTime: 360, endTime: 840 },
    { id: 2, startTime: 840, endTime: 1320 },
    { id: 3, startTime: 1320, endTime: 360 },
  ];

  changeShiftDisabled = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private machineService: MachineService,
    private loaderService: LoaderService,
    private sfcNavigator: SfcNavigatorService,
    private displayConfigurationService: DisplayConfigurationService,
    private operatorAuthenticationService: OperatorAuthenticationService) {
    if (!this.operatorAuthenticationService.isLoggedIn) {
      this.goToMachineSelect();
    }
    this.changeShiftDisabled = this.operatorAuthenticationService.currentUserValue.role == "operatorPlus";
    this.machineId = parseInt(this.route.snapshot.paramMap.get('id'), 10);
    const tempStatus = this.router.getCurrentNavigation().extras.state;
    if (tempStatus !== undefined && 'machine' in tempStatus) {
      if ('selected' in tempStatus) {
        this.status = tempStatus as ComponentStatusModel;
      } else {
        this.machine = tempStatus.machine;
      }
    }
    // this.loaderService.getLoading().subscribe((v) => {
    //   setTimeout(() => this.loading = v);
    // });
  }

  goToSfc(): void {
    this.sfcNavigator.goToSfc();
  }

  async ngOnInit(): Promise<void> {
    this.machineProductionDisplayMethod = await this.displayConfigurationService.getMachineProductionDisplayMethod(this.machineId);
    this.loadProductionData();

    const secondsCounter = interval(60000); // 10s
    // const secondsCounter = interval(30000); // 30s
    // const secondsCounter = interval(1000000000); // inf for debug
    this.subscription = secondsCounter.pipe(takeUntil(this.onDestroy$)).subscribe(n =>{
      if(!this.loading){
        this.loading = true;
        this.loadProductionData();
        this.loadData();
      }
    }
    );
    for (let i = 0; i < 30; i++) {
      const date = moment().subtract(i, 'days').startOf('day');
      if (i === 0) {
        this.dateValue = date.format('YYYY-MM-DD');
      }
      this.dates.push(new SelectItem(date.format('YYYY-MM-DD'), date.format('DD-MM-YYYY')));
    }
    var shiftConfig = this.displayConfigurationService.getMachineShiftsConfiguration(this.machineId);
    shiftConfig.then((result) => {
      this.SHIFTS = [];
      result.shifts.forEach( s => this.SHIFTS.push({ id: s.id, startTime: s.startTime, endTime: s.endTime }))
      this.setUpShifts();
      this.initialized = true;
    });

  }

  ngOnDestroy(): void {
    this.onDestroy$.next();
    this.subscription.unsubscribe();
  }

  loadProductionData(): void {
    if (this.machineProductionDisplayMethod == 0) {
      this.machineService.postApiMachineGetMachineProductionMethod1(this.machineId).subscribe((data) => {
        this.productionData = data;
        // qtyCompleted?: number;
        // qtyRequired?: number;
      })
    }
  }

  setUpShifts(): void {
    const today = moment();
    const currDay = moment().startOf('day');
    this.SHIFTS.forEach(shift => {
      const shiftStart = currDay.clone().add(shift.startTime, 'minutes');
      let shiftEnd = currDay.clone().add(shift.endTime, 'minutes');
      if (shiftEnd < shiftStart) {
        shiftEnd = shiftEnd.add(1, 'days'); // Next day
      }
      this.shifts.push(
        new SelectItem(
          shift.id,
          `${shiftStart.format('HH:mm')} - ${shiftEnd.format('HH:mm')}`,
          null,
          true)
      );
      if (today.isBetween(shiftStart, shiftEnd)) {
        this.shiftValue = shift.id;
      }
    });
    this.activateShiftOptions();
  }

  activateShiftOptions(): void {
    if(this.shiftValue == null){
      this.shiftValue = 1;
    }
    const today = moment();
    const selectedDate = moment(this.dateValue);
    this.SHIFTS.forEach(shift => {
      const shiftStart = selectedDate.clone().add(shift.startTime, 'minutes');
      let shiftEnd = selectedDate.clone().add(shift.endTime, 'minutes');
      if (shiftEnd < shiftStart) {
        shiftEnd = shiftEnd.add(1, 'days'); // Next day
      }
      const selectItem = this.shifts.find(it => it.value === shift.id);
      selectItem.active = today.valueOf() > shiftStart.valueOf();
    });
    if (!this.shifts.find(s => s.value === this.shiftValue).active) {
      this.changeCurrentShiftValue();
    }
  }

  changeCurrentShiftValue(): void {
    const today = moment();
    const selectedDate = moment(this.dateValue);
    this.SHIFTS.forEach(shift => {
      const shiftStart = selectedDate.clone().add(shift.startTime, 'minutes');
      let shiftEnd = selectedDate.clone().add(shift.endTime, 'minutes');
      if (shiftEnd < shiftStart) {
        shiftEnd = shiftEnd.add(1, 'days'); // Next day
      }
      if (today.isBetween(shiftStart, shiftEnd)) {
        this.shiftValue = shift.id;
      }
    });
  }

  async ngAfterViewInit(): Promise<void> {
    while (!this.initialized) {
      await new Promise(f => setTimeout(f, 100));
    }
    if (this.status) {
      this.dateValue = this.status.dateValue;
      this.shiftValue = this.status.shiftValue;
      this.machine = this.status.machine;
      this.selected = this.status.selected.map(row => row.id);
      this.loadData();
    } else if (!this.machine) {
      this.machineService.GetMachine(this.machineId).subscribe(
        (m) => {
          this.machine = { id: m.id, code: m.code, name: m.name, status: m.status, statusHexColor: m.hexColor || '#ffffff' };
          this.loadData();
        },
        (error) => {

        },
        () => {

        }
      );
    } else {
      this.loadData();
    }
  }

  resetData(): void {
    if (this.initialized && !this.changeShiftDisabled) {
      this.activateShiftOptions();
      this.dataSource = [];
      this.selected = [];
      this.loadData();
    }
  }

  loadData(): void {
    this.loading = true;
    this.loaderService.setGlobalLoading(true);
    const data = { dayOfActivity: this.dateValue, machineId: this.machineId, shift: this.shiftValue };
    // console.log('loadData', data);
    this.machineService.postApiMachineGetMachineActivity(data).subscribe(
      (result: MachineActivityDTOs[]) => {
        const tempList = result.map(m => {
          return {
            id: m.id,
            status: m.status,
            statusHexColor: m.statusHexColor || '#ffffff',
            statusReason: m.statusReasons,
            startTime: m.beginDate,
            endTime: m.endDate,
            orders: m.workOnRoutings.map(wor => ({
              orderId: wor.number,
              opNo: wor.operation,
              partNo: wor.partNumber,
              removable: wor.isUnassigningEnabled,
              partDescription: wor.partDescription,
              partDetails: wor.partDetails,
             } as WorkOrderModel)),
            workers: m.workers.map(w => ({ id: w.id, name: w.firstName, surname: w.lastName } as WorkerModel)),
            isReasonRequired: m.statusReasonDetailRequired,
            isOrderRequired: false,
            isWorkerRequired: false
          } as MachineStatusModel;
        });
        this.dataSource = tempList.sort((a, b) => {
          if (a.startTime !== b.startTime) {
            return moment(b.startTime).valueOf() - moment(a.startTime).valueOf();
          }
          if (a.endTime !== b.endTime) {
            return moment(b.endTime).valueOf() - moment(a.endTime).valueOf();
          }
          return b.id - a.id;
        });
        setTimeout(() => {
          this.selected.forEach(id => this.triggerRiggle(id));
          this.loaderService.setGlobalLoading(false);
          this.loading = false;
        });
      },
      (error) => {
        // TODO: catch error
        this.loaderService.setGlobalLoading(false);
        this.loading = false;
      },
      () => {
      }
    );
  }

  getTime(row: MachineStatusModel): string {
    const shift = this.SHIFTS.find(s => s.id === this.shiftValue);

    const currentDay = moment(this.dateValue).startOf('day');
    const shiftStart = currentDay.clone().add(shift.startTime, 'minutes');
    let shiftEnd = currentDay.clone().add(shift.endTime, 'minutes');
    if (shiftEnd < shiftStart) {
      shiftEnd = shiftEnd.add(1, 'days'); // Next day
    }

    const startDate = moment(row.startTime);
    let result = '';
    if (startDate < shiftStart) {
      result += `*${moment().startOf('day').add(shift.startTime, 'minutes').format('HH:mm')}`;
    } else {
      result += `${startDate.format('HH:mm')}`;
    }

    result += ' - ';

    if (row.endTime) {
      const endDate = moment(row.endTime);
      if (endDate > shiftEnd) {
        result += `*${moment().startOf('day').add(shift.endTime, 'minutes').format('HH:mm')}`;
      } else {
        result += `${endDate.format('HH:mm')}`;
      }
    }
    return result;
  }

  private triggerRiggle(id: number): void {
    const rowIndex = this.dataSource.findIndex((row: MachineStatusModel) => row.id === id);
    const matRipple = this.rippleList.toArray()[rowIndex];
    matRipple.launch({
      centered: true,
      persistent: true
    });
  }

  toggleRow(row: MachineStatusModel, event: MouseEvent): void {
    const rowIndex = this.dataSource.indexOf(row, 0);
    const matRipple = this.rippleList.toArray()[rowIndex];
    const id = row.id;
    if (this.selected.includes(id)) {
      const selectedIdx = this.selected.indexOf(id, 0);
      if (selectedIdx > -1) {
        this.selected.splice(selectedIdx, 1);
        matRipple.fadeOutAll();
      }
    } else {
      this.selected.push(id);
      this.triggerRiggle(id);
    }
    event.stopPropagation();
  }

  alphaRipple(hexColor: string): string {
    return alphaRipple(hexColor, 0.5);
  }

  goToMachineSelect(): void {
    this.operatorAuthenticationService.resetTimeout();
    this.router.navigate(['..', '..'], { relativeTo: this.route });
  }

  isAssignDetailsActive(): boolean {
    let result = false;
    if (this.selected.length > 0) {
      result = true;
      const firstRow = this.dataSource.find(row => row.id === this.selected[0]);
      const statusReasonValue = firstRow?.statusReason || null;
      this.selected.forEach(id => {
        const currRow = this.dataSource.find(row => row.id === id);
        const currStatusReason = currRow?.statusReason || null;
        if (currStatusReason !== statusReasonValue) {
          result = false;
        }
      });
    }
    return result;
  }

  isAssignWorkOrderActive(): boolean {
    let result = false;
    if (this.selected.length > 0) {
      result = true;
      const firstRow = this.dataSource.find(row => row.id === this.selected[0]);
      const orders = firstRow?.orders;
      this.selected.forEach(id => {
        const currRow = this.dataSource.find(row => row.id === id);
        if (currRow?.orders?.length !== orders?.length) {
          result = false;
        } else {
          currRow?.orders?.forEach(order => {
            if (!orders.find(o => o.orderId === order?.orderId && o.opNo === order?.opNo)) {
              result = false;
            }
          });
        }
      });
    }
    return result;
  }

  isAssignWorkerActive(): boolean {
    let result = false;
    if (this.selected.length > 0) {
      result = true;
      const firstRow = this.dataSource.find(row => row.id === this.selected[0]);
      const workers = firstRow?.workers;
      this.selected.forEach(id => {
        const currRow = this.dataSource.find(row => row.id === id);
        if (currRow?.workers?.length !== workers?.length) {
          result = false;
        } else {
          currRow?.workers?.forEach(worker => {
            if (!workers.find(w => w.id === worker.id)) {
              result = false;
            }
          });
        }
      });
    }
    return result;
  }

  private getCurrentStatus(): ComponentStatusModel {
    return {
      machine: this.machine,
      shiftValue: this.shiftValue,
      dateValue: this.dateValue,
      selected: this.selected.map(id => this.dataSource.find((row: MachineStatusModel) => row.id === id))
    };
  }

  goToReasonForm(): void {
    this.operatorAuthenticationService.resetTimeout();
    this.router.navigate(['reason'], { state: this.getCurrentStatus(), relativeTo: this.route });
  }

  goToWorkOrderForm(): void {
    this.operatorAuthenticationService.resetTimeout();
    this.router.navigate(['workorder'], { state: this.getCurrentStatus(), relativeTo: this.route });
  }

  goToWorkerForm(): void {
    this.operatorAuthenticationService.resetTimeout();
    this.router.navigate(['worker'], { state: this.getCurrentStatus(), relativeTo: this.route });
  }

}
