import { Injectable } from '@angular/core';
import { MachineDisplayConfiguration } from 'src/app/api/models';
import { MachineService } from 'src/app/api/services';

@Injectable({
  providedIn: 'root'
})
export class DisplayConfigurationService {
  private _cache: Map<number, MachineDisplayConfiguration> = new Map();
  constructor(private machineService: MachineService) {}

  private async getMachineDisplayConfiguration(machineId: number) {
    if (!this._cache.has(machineId)) {
      await this.machineService.postApiMachineGetMachineDisplayConfiguration(machineId)
        .toPromise()
        .then((config) => this._cache.set(machineId, config));
    }
    return this._cache.get(machineId);
  }

  async getMachineWorkOrderAssigningMethod(machineId: number) {
    return (await this.getMachineDisplayConfiguration(machineId)).workOrderAssigningMethod;
  }

  async getMachineProductionDisplayMethod(machineId: number) {
    return (await this.getMachineDisplayConfiguration(machineId)).productionDisplayMethod;
  }

  async getUnassigningWorkOrderEnabled(machineId: number) {
    return (await this.getMachineDisplayConfiguration(machineId)).unassigningWorkOrderEnabled;
  }

  async getAssignedWorkOrderLimit(machineId: number) {
    return (await this.getMachineDisplayConfiguration(machineId)).assignedWorkOrderLimit;
  }

  async getMachineShiftsConfiguration(machineId: number) {
    return (await this.getMachineDisplayConfiguration(machineId)).machineShiftsConfiguration;
  }
}
