import { Component, OnInit } from '@angular/core';
import { LoaderService } from 'src/app/common/services/loader.service';
import { LoadingOverlayRef } from '../loading-overlay/loading-overlay.ref';
import { LoadingOverlayService } from '../loading-overlay/loading-overlay.service';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {
  loading: boolean;
  overlayRef: LoadingOverlayRef = null;
  constructor(private loaderService: LoaderService, private overlay: LoadingOverlayService) {
    this.loaderService.getLoading().subscribe((v) => {
      // console.log(v);
      setTimeout(() => this.loading = v);
      if (v && this.overlayRef === null) {
        // console.log('open overlay');
        this.overlayRef = overlay.open();
        this.overlayRef.afterClosed().subscribe(() => this.overlayRef = null);
      }
      else if (!v) {
        // console.log('close overlay');
        this.overlayRef?.close();
      }
    });
  }

  ngOnInit(): void {
  }

}
