import { Component, HostBinding, Input, OnInit } from '@angular/core';

@Component({
  selector: 'PT-dsr-app-logo-button',
  templateUrl: './dsr-app-logo-button.component.html',
  styleUrls: ['./dsr-app-logo-button.component.scss']
})
export class DsrAppLogoButtonComponent implements OnInit {
  @Input() appName: 'eam' | 'sfc' = 'sfc';
  @HostBinding('style.background') backgroundColor = 'transparent';
  constructor() { }

  ngOnInit(): void {
    this.backgroundColor = this.getAppColor();
  }

  private getIconFileName(): string {
    switch(this.appName) {
      case 'eam':
        return 'eam_mini_logo.svg';
      case 'sfc':
        return 'sfc_mini_logo.svg';
    }
  }

  private getAppColor(): string {
    switch(this.appName) {
      case 'eam':
        return '#069779';
      case 'sfc':
        return '#009FDB';
    }
  }

  getIconPath(): string {
    return '/assets/' + this.getIconFileName();
  }

}
