import { Injectable } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { WorkerLoginResult } from 'src/app/api/models';
import { AdministrationService } from 'src/app/api/services';
import { MachineSelectComponent } from 'src/app/areas/machine/machine-select/machine-select.component';
import { NotificationService } from 'dsr-toolkit';
import { FocusRefreshService } from './focus-refresh.service';

@Injectable({
  providedIn: 'root'
})
export class OperatorAuthenticationService {
  private loggedUser: any = null;
  private currentUserSubject: BehaviorSubject<WorkerLoginResult>;
  private currentWorkerSubject: BehaviorSubject<WorkerLoginResult>;
  public currentUser: Observable<WorkerLoginResult>;
  public currentWorker: Observable<WorkerLoginResult>;

  private timeoutValue: number = 0;
  private timeLeft: number = 0;
  private interval: any;

  constructor(
    private administrationService: AdministrationService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService,
    private focusRefreshService: FocusRefreshService,
  ) {
    const logoutTimeoutValue = localStorage.getItem(MachineSelectComponent.LOGOUT_TIMEOUT_KEY);
    this.changeTimeout(JSON.parse(logoutTimeoutValue ?? '0') as number);

    this.currentUserSubject = new BehaviorSubject<WorkerLoginResult>(null);
    this.currentUser = this.currentUserSubject.asObservable();

    this.currentWorkerSubject = new BehaviorSubject<WorkerLoginResult>(null);
    this.currentWorker = this.currentWorkerSubject.asObservable();
  }

  public get currentUserValue(): WorkerLoginResult {
    return this.currentUserSubject.value;
  }

  public get secondsLeft(): number {
    return this.timeLeft;
  }

  public get isLoggedIn(): boolean {
    console.log('isLoggedIn', this.loggedUser, this.loggedUser != null)
    return this.loggedUser != null;
  }

  public login(operatorLoginCode: string) {
    this.administrationService.postApiAdministrationLoginWorker(operatorLoginCode).subscribe(user => {
      if(user?.role != "operator"){
        this.loggedUser = user;
        this.resetTimeout();
        console.log(this.loggedUser);
        this.currentUserSubject.next(this.loggedUser);
      }
      else{
        this.currentWorkerSubject.next(user);
      }
    },
      error => {
        console.log(error);
        if(error.status == 401)
          this.notificationService.showErrorTranslated('machine.loginFailed', 'Ok', null, false, 4000);
        if(error.status == 403)
          this.notificationService.showErrorTranslated('machine.inactiveWorker', 'Ok', null, false, 4000);
      }
    )
  }

  public logout() {
    this.loggedUser = null;
    this.resetTimeout();
    this.currentUserSubject.next(this.loggedUser);
    this.focusRefreshService.reFocus();
    this.router.navigate(['/']);
  }

  public resetTimeout() {
    if (this.interval) {
      clearInterval(this.interval);
    }
    this.timeLeft = this.timeoutValue;
    if (this.loggedUser && this.timeoutValue > 0) {
      this.interval = setInterval(() => {
        if(this.timeLeft > 0) {
          this.timeLeft--;
        } else {
          this.logout();
        }
      },1000);
    }
  }

  public changeTimeout(timeout: number) {
    if (timeout < 0) {
      timeout = 0;
    }
    this.timeoutValue = timeout;
    this.resetTimeout();
  }
}
