import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NotificationService, SelectItem, SelectItemInterface } from 'dsr-toolkit';
import { SetStatusReasonModel } from 'src/app/api/models/set-status-reason-model';
import { MachineService } from 'src/app/api/services';
import { LoaderService } from 'src/app/common/services/loader.service';
import { OperatorAuthenticationService } from 'src/app/common/services/operator-authentication.service';
import { SfcNavigatorService } from 'src/app/common/services/sfc-navigator.service';
import { ComponentStatusModel } from '../models/component-status.model';

@Component({
  selector: 'app-machine-status-reason-form',
  templateUrl: './machine-status-reason-form.component.html',
  styleUrls: ['./machine-status-reason-form.component.scss']
})
export class MachineStatusReasonFormComponent implements OnInit {
  loading = true;
  status: ComponentStatusModel = null;
  selectedValue: number = null;
  statusReasonValues: SelectItemInterface[] = [];

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private machineService: MachineService,
    private loaderService: LoaderService,
    private notificationService: NotificationService,
    private sfcNavigator: SfcNavigatorService,
    private operatorAuthenticationService: OperatorAuthenticationService) {
    this.status = this.router.getCurrentNavigation().extras.state as ComponentStatusModel;
    if (this.status == null || !this.operatorAuthenticationService.isLoggedIn) {
      this.goToMachineStatus();
    }
    this.loaderService.getLoading().subscribe((v) => {
      setTimeout(() => this.loading = v);
    });
  }

  goToSfc(): void {
    this.sfcNavigator.goToSfc();
  }

  ngOnInit(): void {
    var test = this.status.selected.map((x) => x.id);
    this.machineService.postApiMachineGetStatusReasons(test).subscribe(
      (result) => {
        this.statusReasonValues = result.map((dictItem) => new SelectItem(dictItem.id, dictItem.key, dictItem.name));
        this.statusReasonValues.sort((a,b)=>a.text.localeCompare(b.text));
        if (this.status.selected?.length > 0) {
          const reasonName = this.status.selected[0].statusReason;
          if (reasonName) {
            this.selectedValue = this.statusReasonValues.find(sr => sr.details === reasonName).value as number;
            // console.log(reasonName);
            // console.log(this.statusReasonValues, this.statusReasonValues.find(sr => sr.details === reasonName), this.selectedValue);
          }
        }
      },
      (error) => {
        // TODO: Catch error
      },
      () => {
      }
    );
  }

  goToMachineStatus(): void {
    this.operatorAuthenticationService.resetTimeout();
    this.router.navigate(['..'], { state: this.status, relativeTo: this.route });
  }

  changeSelectedStatus(reason: SelectItemInterface)
  {
    this.selectedValue=reason.value as number;
    this.save();
  }

  save(): void {
    const data: SetStatusReasonModel = {
      machineActivityIds: this.status.selected.map(m => m.id),
      statusReasonId: this.selectedValue
    };
    this.machineService.postApiMachineSetStatusReason(data).subscribe(
      () => {
        this.status.selected = [];
        this.notificationService.showSuccessTranslated('common.savedSuccessfully', 'Ok', null, false, 4000);
      },
      () => {},
      () => this.goToMachineStatus()
    );
  }
}
