import { Component, OnInit, Inject } from '@angular/core';
import { WorkOrderService } from 'src/app/api/services';
import { WorkOrderRoutingItemViewModel } from 'src/app/api/models/work-order-routing-item-view-model';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { GeneralPromptDialogComponent } from 'dsr-toolkit';

@Component({
  selector: 'PT-select-work-order-panel-dialog',
  templateUrl: './select-work-order-panel-dialog.component.html',
  styleUrls: ['./select-work-order-panel-dialog.component.scss']
})
export class SelectWorkOrderPanelDialogComponent implements OnInit {

  workOrders: WorkOrderRoutingItemViewModel[] = [];
  loading = false;

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
  protected matDialogRef: MatDialogRef<GeneralPromptDialogComponent>,
  private workOrderService: WorkOrderService) { }

  ngOnInit(): void {
    this.loading = true;
    this.workOrderService.postApiWorkOrderGetWorkOrderRoutingList(this.data.machineId as number).subscribe(
      (result: WorkOrderRoutingItemViewModel[]) => {
        this.workOrders = result;
      },
      () => {  },
      () => { this.loading = false; }
    );
  }

  clickCancel(): void {
    this.matDialogRef.close(null);
  }

  selectkWorkOrder(workOrder: WorkOrderRoutingItemViewModel): void {
    this.matDialogRef.close(workOrder);
  }

}
