import { BrowserModule } from '@angular/platform-browser';
import { APP_INITIALIZER, Injector, NgModule } from '@angular/core';

import { LOCATION_INITIALIZED, registerLocaleData } from '@angular/common';
import { LOCALE_ID } from '@angular/core';
import localePl from '@angular/common/locales/pl';
import localeUk from '@angular/common/locales/uk';
import localeEn from '@angular/common/locales/en-GB';
registerLocaleData(localePl);
registerLocaleData(localeUk);
registerLocaleData(localeEn);

import { TranslateModule, TranslateLoader, TranslateService, TranslateCompiler } from '@ngx-translate/core';
import { TranslateMessageFormatCompiler } from 'ngx-translate-messageformat-compiler';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpClient, HTTP_INTERCEPTORS } from '@angular/common/http';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { DsrToolkitModule, LanguageService } from 'dsr-toolkit';
import { PTCommonModule } from './common/PT-common.module';
import { LayoutModule } from './layout/layout.module';
// tslint:disable-next-line: typedef
export function appInitializerFactory(translate: TranslateService, injector: Injector, languageService: LanguageService) {
  return () => new Promise<any>((resolve: any) => {
    const locationInitialized = injector.get(LOCATION_INITIALIZED, Promise.resolve(null));
    locationInitialized.then(() => {
      const langToSet = translate.getBrowserLang();
      translate.setDefaultLang('pl');
      languageService.setCurrent(langToSet);
      translate.use(langToSet).subscribe(() => {
        // tslint:disable-next-line: no-console
        console.info(`Successfully initialized '${langToSet}' language.'`);
      }, err => {
        console.error(`Problem with '${langToSet}' language initialization.'`);
      }, () => {
        resolve(null);
      });
    });
  });
}

export function HttpLoaderFactory(http: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader(http);
}

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    DsrToolkitModule,
    LayoutModule,
    TranslateModule.forRoot({
      compiler: {
        provide: TranslateCompiler,
        useClass: TranslateMessageFormatCompiler
      },
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      },
      isolate: false
    }),
  ],
  providers: [
    {
      provide: APP_INITIALIZER,
      useFactory: appInitializerFactory,
      deps: [TranslateService, Injector, LanguageService],
      multi: true
    },
    LanguageService,
    {
      provide: LOCALE_ID,
      deps: [LanguageService],
      useFactory: languageService => languageService.getCurrent()
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
