import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FocusRefreshService {

  private focusSubject: BehaviorSubject<boolean>;
  public focus : Observable<boolean>;
  constructor() {
    this.focusSubject = new BehaviorSubject<boolean>(false);
    this.focus = this.focusSubject.asObservable();
  }

  public reFocus()
  {
    this.focusSubject.next(false);
    setTimeout(()=> {this.focusSubject.next(true)})
  }
}
