import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MachineSelectComponent } from './machine-select/machine-select.component';
import { MachineStatusListComponent } from './machine-status-list/machine-status-list.component';
import { MachineStatusReasonFormComponent } from './machine-status-reason-form/machine-status-reason-form.component';
import { MachineStatusWorkOrderFormComponent } from './machine-status-work-order-form/machine-status-work-order-form.component';
import { MachineStatusWorkerFormComponent } from './machine-status-worker-form/machine-status-worker-form.component';

export const machineRoutes: Routes = [
  { path: '', component: MachineSelectComponent },
  { path: ':id/status', component: MachineStatusListComponent },
  { path: ':id/status/reason', component: MachineStatusReasonFormComponent },
  { path: ':id/status/workorder', component: MachineStatusWorkOrderFormComponent },
  { path: ':id/status/worker', component: MachineStatusWorkerFormComponent },
  { path: '**', redirectTo: '' }
];

@NgModule({
  imports: [RouterModule.forChild(machineRoutes)],
  exports: [RouterModule]
})
export class MachineRoutingModule { }
